.alerta {
  padding: .5rem;
  text-align: center;
  font-weight: bold;
  color: $blanco;
  margin-top: 1rem;

  &.error {
    background-color: $rojo;
  }

  &.exito {
    background-color: $verde;
  }
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;

  thead {
    background-color: $verde;
    th {
      color: $blanco;
      padding: 2rem;
    }
  }

  .imagen-tabla {
    width: 10rem;
  }
}

.imagen-small {
  width: 20rem;
}


